@charset "utf-8";

*{
  padding: 0px;
  margin: 0px;
}

.create{
  width: 100%;
  height: 230px;
  position: fixed;
  z-index: 50;
  box-shadow: 0px 0px 13px #00000099;
  background-color: #fff;
  top: 0px;
}

.create p{
  width: 100%;
  height: 30px;
}

.logo{
  background-image: url(../../../public/logo_ko.png);
  height: 50px;
  width: 250px;
  margin: 0px auto;
  margin-top: 15px;
  background-position: 11px -30px;
  background-repeat: no-repeat;
}

.c_txt01{
  margin: 0px auto;
  margin-top: 10px;
  width: 96%;
  position: relative;
  text-indent: 2%;
  font-size: 14px;
}
.c_txt01 input{
  position: absolute;
  right: 5px;
  height: 25px;
  width: 65%;
  text-indent: 5px;
}
