.item_box{
  position: relative;
  /* background-color: #000; */
  top: 230px;
  overflow: hidden;
  /* width: 100px; */
}


.product_list{
  /* transform: translateY(400px); */
  list-style: none;
  background-color: #fff;
  position: relative;
  height: 120px;
  width: 95%;
  margin: 20px auto;
  overflow: hidden;
  padding-inline-start: 0px;
  border-radius: 10px;
  box-shadow: 0px 3px 20px #00000099;
  transition: .2s;
  /* padding: 20px; */
}

.product_list:hover{
  transform:scale(1.05);
}
.product_list img{
  /* width:325px;height:212px; */
  height: 120px;
  position: absolute;
  top: 0px;
  left: -50px;
}

.text01{
  /* position: absolute; */
  margin-top: 12px;
  left: 40px;
  font-size: 14px;
  width: 55%;
  transform: translateX(120px);
  font-weight: bold;
  color: #333;
}

.text02{
  position: absolute;
  margin-top: 20px;
  left: 120px;
  bottom: 10px;
  font-size: 14px;
  width: 50%;
  /* transform: translateX(120px); */
}


.product_list input{
  position: absolute;
  top: 0px;
  right: 0px;
  margin: 0px;
  padding: 0px;
  background-color: rgb(247, 16, 0);
  font-size: 11px;
  border: none;
  width: 50px;
  cursor: pointer;
  color: #fff;
  font-weight: bold;
  height: 120px;
}
